//      Loader start
.loader {
  background: none repeat scroll 0 0 #ffffff;
  position: absolute;
  top: 46px;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 250px;
}

.loaderInner {
  background-image: url("../img/preloader.gif");
  background-repeat: no-repeat;
  background-position: center center;
  background-color: #fff;
  height: 60px;
  width: 60px;
  margin-top: -30px;
  margin-left: -30px;
  left: 50%;
  top: 50%;
  position: absolute;
}

//       Loader end
.clrLn:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}

a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}

//      General settings of the box start
.boxWrapper {
  width: 555px;
  height: 300px;
  margin: 15px;
  display: inline-block;
  border: 1px solid #E7E6E6;
  border-radius: 4px;
  float: left;
  position: relative;
}

.titleBox {
  background: #E7E6E6;
  width: 100%;
  padding: 10px 20px 10px 20px;
  h2 {
    font: 24px/26px Arial, Helvetica, sans-serif;
    color: #000;
    margin: 0;
    display: inline-block;
  }
}

.boxContent {
  padding: 45px 15%;
}

//      General settings of the box end

//      Companies number start
.totalCompanies {
  width: 170px;
  min-width: 170px;
  height: 170px;
  min-height: 170px;
  margin: 0 auto;
  display: table;
  border: 2px solid #E7E6E6;
  border-radius: 50%;
  font: 35px/38px Arial, Helvetica, sans-serif;
  span {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
}

//      Companies number end
//      Companies list start
#listCompanies {
  height: 240px;
  padding: 20px;
  font: 18px/22px Arial, Helvetica, sans-serif;
  display: block;
  overflow-y: scroll;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      display: block;
      width: 100%;
      border: 1px solid #000;
      padding: 10px;
      cursor: pointer;
    }
    .active {
      background-color: #e7e6e6;
    }
  }
}

//      Companies list end
//      Company partners start
.blockWrap {
  margin: 15px;
  display: block;
  border: 1px solid #E7E6E6;
  border-radius: 4px;
}

.controls {
  float: right;
  h2 {
    float: left;
    font: 18px/22px Arial, Helvetica, sans-serif;
    display: inline-block;
    margin: 0 5px 0 0;
  }
}

#sort,
#percent {
  float: left;
  margin: 0 10px 0 0;
  h3 {
    font: 18px/22px Arial, Helvetica, sans-serif;
    margin: 0;
    display: inline-block;
  }
}

#sort,
#percent {
  margin-right: 20px;
  position: relative;
  .fa-caret-up,
  .fa-caret-down {
    position: absolute;
    cursor: pointer;
  }
  .fa-caret-up {
    top: -2px;
    right: -12px;
  }
  .fa-caret-down {
    bottom: -2px;
    right: -12px;
  }
}

.blockWrap {
  display: none;
}

#partnersList {
  padding: 50px 10px 10px 10px;
  margin: 0;
  list-style: none;
  overflow-x: auto;
  white-space: nowrap;
  li {
    display: inline;
    margin: 0 10px 0 0;
    white-space: nowrap;
  }
}

.partnerContainer {
  display: inline-block;
  width: 100px;
  height: 420px;
}

.percentBox {
  width: 100px;
  height: 100px;
  margin: 0 0 169px 0;
  display: table;
  border: 1px solid #000;
  border-radius: 50%;
  position: relative;
  h2 {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  &:after {
    content: '';
    position: absolute;
    width: 10px;
    height: 80px;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    bottom: -79px;
    right: 45px;
  }
}

.partnerBox {
  width: 250px;
  height: 70px;
  padding: 10px;
  margin: 0 0 0 -75px;
  border: 1px solid #000;
  display: table;
  transform: rotate(270deg);
  h2 {
    display: table-cell;
    margin: 0;
    text-align: center;
    vertical-align: middle;
  }
}

//      Company partners end
//      Companies by location start
#listLocations {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    margin: 5px 0;
  }
}

#modalForm {
  width: 500px;
  height: 500px;
  border-radius: 5px;
  border: 3px #000 solid;
  background: #fff;
  position: fixed;
  top: 45%;
  left: 50%;
  margin-top: -250px;
  margin-left: -250px;
  display: none;
  opacity: 0;
  z-index: 5;
  padding: 20px 30px;
  overflow-y: auto;
}

#modalForm #modalClose {
  width: 21px;
  height: 21px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  display: block;
}

#modalClose {
  i {
    font-size: 20px;
  }
}

#overlay {
  z-index: 3;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  -moz-opacity: 0.8;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}

//      Companies by location end
//      News box start
.swiper-slide {
  width: 553px;
  box-sizing: border-box;
  padding: 15px;
  img {
    width: 180px;
    height: 160px;
    margin: 0 0 15px 0;
    float: left;
  }
}

.newsContent {
  float: right;
  width: 60%;
}

.boxPublic {
  clear: both;
}

//      News box end
//      Form content start
.formWrapper {
  max-width: 500px;
  border: 1px solid #E7E6E6;
  border-radius: 4px;
  margin: 15px 0 20px 29%;
}

.formContainer {
  padding: 20px 15px 15px 15px;
  position: relative;
  input[type="text"],
  input[type="email"],
  input[type="password"],
  select {
    font: 16px/18px Arial, Helvetica, sans-serif;
    margin: 0 auto 15px;
    padding: 5px 10px 5px 50px;
    width: 400px !important;
    border: 1px solid #E7E6E6;
    border-radius: 5px;
    display: block;
  }
  select {
    padding: 3px 10px 3px 50px;
  }
  input[type="submit"] {
    padding: 10px 25px;
    display: block;
    float: right;
    margin: 0 7% 15px 0;
    border: 0px solid turquoise;
    border-radius: 5px;
  }
}

.inputWrapper {
  position: relative;
}

label {
  margin: 0;
  text-align: center;
  vertical-align: middle;
  padding: 5px 0 0 0;
  cursor: pointer;
  .fa {
    font-size: 18px;
    color: #fff;
  }
}

.labelName,
.labelSecond,
.labelEmail,
.labelPass {
  position: absolute;
  width: 30px;
  left: 34px;
  height: 30px;
  background: #696969;
  border-radius: 5px 0 0 5px;
}

#regulations {
  margin-left: 8%;
}

.granted {
  border: 2px solid green !important;
}

.denied {
  border: 2px solid red !important;
}

.error {
  background-color: red;
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 5px;
  display: inline-block;
  margin-left: 7%;
  margin-bottom: 15px;
  border: 0px solid;
  border-radius: 6px;
}

//      Form content start

