/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  //      General settings of the box start
  .boxWrapper {
    width: 455px;
  }
  //      General settings of the box end
  //      News box start
  .swiper-slide {
    width: 453px;
    img {
      width: 160px;
      height: 150px;
    }
  }
  //      News box end
  //      Form content start
  .formWrapper {
    margin-left: 24%;
  }
  //      Form content end
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  //      General settings of the box start
  .boxWrapper {
    width: 345px;
  }
  //      General settings of the box end
  //      News box start
  .swiper-slide {
    width: 345px;
    img {
      width: 130px;
      height: 110px;
    }
  }
  .boxPublic {
    clear: left;
  }
  .newsContent {
    width: 55%;
  }
  .swiper-container-horizontal {
    .swiper-pagination-bullets {
      bottom: 0;
    }
  }
  //      News box end
  //      Form content start
  .formWrapper {
    margin-left: 16%;
  }
  //      Form content end
}

/* Small Devices, Tablets */
@media only screen and (max-width: 767px) {
  //      General settings of the box start
  .boxWrapper {
    width: 94%;
  }
  //      General settings of the box end
  //      News box start
  .swiper-slide {
    width: 345px;
    img {
      width: 170px;
      height: 170px;
      margin: 0 15px 15px 0;
    }
  }
  .newsContent {
    width: 55%;
  }
  //      News box end
  //      Form content start
  .formWrapper {
    max-width: 94%;
    margin: 15px;
  }
  .formContainer {
    input[type="text"],
    input[type="email"],
    input[type="password"] {
      width: 100% !important;
    }
  }
  .labelName,
  .labelSecond,
  .labelEmail,
  .labelPass {
    left: 0;
  }
  .formContainer {
    select {
      width: 100% !important;
    }
  }
  #regulations {
    margin-left: 0%;
  }

  .submit {
    margin: 0 0 10px 0;
  }
  .error {
    margin-left: 0;
  }
  //      Form content end
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  //      General settings of the box start
  .boxWrapper {
    width: 91%;
    height: auto;
  }
  //      General settings of the box end
  //      Company partners start
  .controls {
    margin: 10px 0 0 0;
    float: left;
  }
  //      Company partners end
  //      News box start
  .swiper-slide {
    img {
      width: 190px;
      height: 190px;
      margin: 0;
    }
  }
  .newsContent {
    width: 95%;
    float: left;
  }
  .swiper-container-horizontal {
    .swiper-pagination-bullets {
      bottom: 10px;
      left: 40px;
    }
  }
  //      News box end
}